const propgetScale = (width, height) => {
  const scale = Math.max(width, height) / 1000;
  return Math.min(1, Math.min(scale, 1.1));
};
  

  const RotatingProp = ({ width, height }) => {
    const [rotation, setRotation] = useState(0);
  
    useLayoutEffect(() => {
      const intervalId = setInterval(() => {
        setRotation(rotation + 0.09);
      }, 1000 / 60); // Adjust the interval based on the desired frame rate
  
      return () => {
        clearInterval(intervalId);
      };
    }, [rotation]);
  
    return (
      <Sprite
        image={prop}
        anchor={0.5}
        rotation={rotation}
        x={width * 0.5}
        y={height * 0.3}
        scale={propgetScale(width, height)}
      />
    );
  };
  
  export default RotatingProp;