import React, { useEffect, useState } from 'react';
import { Sprite, TilingSprite, useTick } from '@pixi/react';
import * as PIXI from 'pixi.js';
import dottedlineHorizontal from '../assets/dotted-line-horizontal.svg'
import dottedlineVertical from '../assets/dottedlineVertical.svg'


const TilingHorizontaLine = ({ width, height, gameStatus }) => {
  const [position, setPosition] = useState({ x: width * 0.5, y: height*0.97 });
  const [x, setX] = useState(width*0.5);
  const [y, setY] = useState(height*0.97);
//   const getScale = (width, height) => {
//     const scale = Math.min(width, height) / 1000;
//     return Math.min(scale, 1);
//   };
  useTick((delta) => {
      if (gameStatus === 2) {
        // setPosition(prevPosition => ({
        //   x: prevPosition.x - 0.5*delta,
        // //   y: prevPosition.y,
        // }));

        setX(prevPosition => (prevPosition-0.5));
      }
    }, [width, height, gameStatus]);

  return (
      <>
      <TilingSprite
        image={dottedlineVertical}
        // width={width}
        // height={height}
        // tilePosition={position}
        x={x}
        y={y}
        // alpha={0.7}
        anchor={0}
        
        />
        {/* <Sprite width={width} height={height}  image={bg} scale={getScale(width, height)}  />      */}
        </>
  );
};

export default TilingHorizontaLine;
