import React, {useRef, useState, useLayoutEffect, useEffect} from 'react'   
import * as PIXI from 'pixi.js'
import { useTick, AnimatedSprite, Container } from '@pixi/react';
import plane from '../assets/prop.svg'
import {flyPlaneFrames } from '../assets/plane';


/** Flying Rocket Animation Sequence */
const flyPlaneTextureArray = flyPlaneFrames.map((value)=>{ 
  const texture = PIXI.Texture.from(value);
  return texture;

})

const planegetScale = (width, height) => {
  const scale = Math.min(width, height) / 1000;
  return Math.max(0.8, Math.min(scale, 1));
};


const roundValues =(val)=>{
  return Math.ceil(val);
}


const FlyingPlane = ({width, height}) => {
  const [rotation, setRotation] = useState(0);
  const [containerWidth, setContainerWidth] = useState(width*0.01);
  const [containerHeight, setContainerHeight] = useState(height*0.97);
  
  // useTick((delta) => delta && setRotation(rotation + 0.09 * delta));
  // Rotating the propeller 
  useLayoutEffect(() => {
    const intervalId = setInterval(() => {
      setRotation(rotation + 0.09);
    }, 1000 / 60); // Adjust the interval based on the desired frame rate

    return () => {
      clearInterval(intervalId);
    };
  }, [rotation]);

  useEffect(()=>{
    
  })

  return (
    <Container  position={[width*0.03, height*0.94]} >
        <AnimatedSprite
        anchor={[0,1]}
        scale={planegetScale(width, height)}
        textures={flyPlaneTextureArray}
        isPlaying={true}
        initialFrame={0}
        loop={true}
        animationSpeed={0.2}
        />
      </Container>
  );
};


  export default FlyingPlane;
  