import React, {useEffect, useRef, useState} from "react";
import * as PIXI from 'pixi.js';
import { Sprite, Text, Container, ParticleContainer, useTick } from "@pixi/react";
import cometBlue from './assets/cometBlue.png'
import cometBig from "./assets/Comet.png";
import cometRedOrange from "./assets/cometRedOrange.png";

const textStyle = new PIXI.TextStyle({
    align : 'left', 
    fill: "#26f7a3",
    letterSpacing: 3,
  })
  

const CometAnimation = ({width, height}) =>{
    const cometContainerRef  = useRef(null);
    const [cometTextures, setCometTextures] = useState([cometBlue, cometBig, cometRedOrange]);

    
    useEffect(()=>{
        
        const cometContainer = cometContainerRef.current;
        if(cometContainer && cometContainer.children.length === 0){
            const numComet = 4;
            for(let i =0; i< numComet; i++){
                const randomTexture = cometTextures[Math.floor(Math.random() * cometTextures.length)];
                console.log(randomTexture);
                console.log('fdfd', Math.floor(Math.random() * cometTextures.length));
                const comet =  new PIXI.Sprite(PIXI.Texture.from(randomTexture));
                comet.anchor.set(0.5);
        
                // set random position for each star 
                comet.x = Math.random() * width;
                comet.y = Math.random() * height;
        
                // Set randome scale for each comet
                comet.scale.set(0.1 + Math.random()*0.2);
                
                // Add comet sprite to the particle container
                cometContainer.addChild(comet);
            }

        }

        return () =>{
            console.log("comet container unmounted --------")

            if(cometContainer){
                console.log("comet container unmounted")
                cometContainer.destroy({children: true});
            }

        }
    },[width, height, cometTextures]);

    useTick((delta)=>{
        cometContainerRef.current.children.forEach((comet) => {
            comet.position.x -=1 * delta;
            comet.position.y += 1 *delta;

            // Check if comets are outside the canvas on the bottom left
            if(comet.position.x <0 && comet.position.y > height){

                comet.position.x = Math.random() * width+(width*0.3);
                comet.position.y = -Math.random() * height*0.1;
            }
            
        });
    });

    return(
        <>
            <Container>
                <Text text="Comet and Stars Animation" style={textStyle} />
                {/* <Sprite image={cometBlue}  scale={0.5}  ></Sprite> */}
            </Container>
        <ParticleContainer
         ref={cometContainerRef}
         properties={{
             scale: true,
             position: true,
             rotation: true,
             alpha: true
            }}
           />
             
            </>
      
    );
}


export default CometAnimation;