import React, {useRef, useState, useEffect} from 'react'   
import * as PIXI from 'pixi.js'
import { Stage, Text, Container, useTick, Sprite, AppConsumer } from '@pixi/react';
import blur from '../assets/blur.svg'


const BlurBg = ({}) => {
    const [rotation, setRotation] = useState(0);
    const [bgTint, setBgTint] = useState(0xC017B4);
    // const [isAnimate, setIsAnimate] = useState(play);
    
    // Blur bg tint 
    // x < 2x --> tinti color 0x34B4FF -->dark Blue
    // 2x < x > 10x --> tint colro 0x913EF8  --> dark purple
    // setBgTint(0x913EF8); 
    // 10x < x  --> tint colro 0xC017B4  --> dark pink 
    // setBgTint(0xC017B4); 
    

    // useTick((delta) => (isAnimate) ? delta && setRotation(rotation + 0.004 * delta):'');
  
    return (
      <Sprite
        image={blur}
        anchor={0.5}
        tint={bgTint}
        // rotation={rotation}
      />
    );
  };

export default BlurBg;