import React, { useEffect, useState } from 'react';
import {  Sprite, Container, useTick } from '@pixi/react';
import line from '../assets/line.svg'
import dottedlineHorizontal from '../assets/dotted-line-horizontal.svg'
import dottedlineVertical from '../assets/dottedlineVertical.svg'

function Axis({width, height}) {
  // const [position, setPosition] = useState({ x: width*0.5, y: height*0.97 });
  
  return (

    <>

        <Container  rotation={90 * (Math.PI/180)}  position={[width*0.03, -(height+height*0.045)]} >
            <Sprite  image={line} anchor={0}  />
        </Container>
        <Container  position={[width*0.012, height-height*0.6]} >
            <Sprite  image={dottedlineVertical} anchor={0.5}  />
        </Container>
        <Container  position={[width*0.03, height*0.94]} >
            <Sprite  image={line} anchor={0}  />
        </Container>
      <MovingDottedLine width={width} height={height} />
     
      
    </>
  
    );
}
function MovingDottedLine({ width, height }) {
  let spriteX = width * 0.5;
  const xSpeed = 0.8; // Adjust the speed of the movement

  useTick((delta) => {
    // Update the x-coordinate of the sprite continuously
    spriteX -= xSpeed * delta;
    // if (spriteX < -431) {
    //   // Reset the x-coordinate to create the endless effect
    //   spriteX += 862;
    // }
  });

  return (
      <Sprite image={dottedlineHorizontal} anchor={0.5} x={spriteX} y={height*0.97} />
  
  );
}
export default Axis;