import React, {useRef, useState, useEffect} from 'react'   
import * as PIXI from 'pixi.js'
import { Stage, Text, Container, useTick, Sprite, AppConsumer } from '@pixi/react';
import bgSun from '../assets/bg-sun.svg'


const RotatingSun = ({play}) => {
    const [rotation, setRotation] = useState(0);    
    useTick((delta) => (play) ? delta && setRotation(rotation + 0.004 * delta):'');
  
    return (
      <Sprite
        image={bgSun}
        anchor={0.5}
        rotation={rotation}
      />
    );
  };



  export default RotatingSun;
  